<template>
  <div class="login">
    <h1>Login</h1>
    <form @submit.prevent="login">
      <label>
        <input class="form-control" v-model="user.email" type="text" placeholder="Email">
      </label>
      <label>
        <input class="form-control" v-model="user.password" type="password" placeholder="Password">
      </label>
      <b-button type="submit" class="btn btn-success btn-block">Acceder</b-button>
    </form>
    <div>
      <p v-if="message !== ''">{{ message }}</p>
    </div>
  </div>
</template>

<script>
  import {mapActions} from 'vuex'
  export default {
    name: "Login",
    data() {
      return {
        user: {email: '', password: ''},
        message: ''
      }
    },
    methods: {
      ...mapActions(['saveUser']),
      login() {
        this.axios.post('/login', this.user)
        .then(response => {
          this.message = ''
          const token = response.data.token
          this.$store.dispatch('saveUser', token)
        })
        .catch(error => {
          this.message = error.response.data.message
        })

      }
    }
  }
</script>

